              <div class="flex flex-col space-y-1">
                <div class="flex flex-row align-center justify-around bg-orange-300 p-1 w-full p-2.5">
                  <div><img @error="notFound" :src="`${value.data.data.profile.image}`" alt="" class="w-14 h-14 rounded-full border border-1 border-white"></div>
                  <div class="flex flex-col">
                    <div class="flex flex-row">
                      <font-awesome-icon :icon="['fas','male']" class="bg-slate-400 w-6 h-6 bg-white mx-2 rounded-full"></font-awesome-icon>
                      <span class="text-black text-sm">{{value.data.data.profile.localized_position}}</span>
                    </div>
                    <div><p class="font-semibold text-center text-black">{{value.data.data.profile.name_fa}}</p></div>
                  </div>
                </div>

                <div class="bg-white p-1 flex flex-col space-y-1 p-2">
                  <p class="font-semibold text-start w-full m-1">ملیت : {{value.data.data.profile.nationality}}</p>
<!--                  <p class="font-semibold text-start w-full m-1">گل:{{value.data.data.statistics.items[1].value}}</p>-->
<!--                  <p class="font-semibold text-start w-full m-1">پاس گل :{{value.data.data.statistics.items[2].value}}</p>-->
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {ref} from 'vue'
export default {
  name: "ExampleSuperGoal",
  props:{
    myList:{
      type:Array,
      required:true
    }
  },
  components:{FontAwesomeIcon},
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
   notFound,notShow
    }
  }
}
</script>

